.Blog{
    width: 100%;
    height: auto;
    min-height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    overflow-x: hidden;
    scroll-behavior: smooth;
}


.Blog .box{
    margin-top: 10vh;
    width: 78%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.Blog .box img{
    width: 100%;
    height: 50%;
    border-radius: 16px;
    box-shadow: 0 8px 30px rgb(0,0,0,0.22);
}
.Blog .box h1{
    width: 100%;
    text-align: left;
    font-size: 1.6rem;
    font-weight: bold;
    opacity: 0.9;
    line-height: 29px;
    margin-bottom: 0.3rem;
    word-wrap: break-word;
    box-decoration-break: clone;
}
.Blog .box .queue{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 0.3rem;
    width: 100%;
}
.Blog .box p{
    text-align: left;
    margin-top: 0rem;
    margin-bottom: 0.3rem;
}

.markdown{
    max-width: 78%;
    width: 78%;
}

.markdown code {
    color: grey;
    background-color:#FFEFF0;
    border-radius:4px;
    /* border: 1px solid grey; */
    /* padding: 1rem; */
    /* background-color: ; */
  }
  .markdown img{
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
    height: auto;
  }
  .markdown  pre {
    background-color: #282C34;
    border-radius: .4rem;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }

.markdown mark{
background-color:yellow;
    border-radius:4px;
}
  .markdown pre code {
      background-color:yellow;
    color: rgb(209, 209, 209);
  }
  
  .markdown h2,
  .markdown h3,
  .markdown h4,
  .markdown h5,
  .markdown h6 {
    color: black;
    font-weight: bold;
    white-space: nowrap;
  }
  
  .markdown pre code {
    display: block;
    background: none;
    white-space: pre;
    -webkit-overflow-scrolling: touch;
    overflow-x: scroll;
    max-width: 100%;
    min-width: 100px;
    padding: 0;
  }
  
  .markdown {
    font-size: 1.08rem;
    line-height: 1.625; 
    font-weight: 500;
  }
  .markdown p,
  .markdown ul,
  .markdown ol,
  .markdown blockquote {
    margin-top: 6;
    margin-bottom: 6;
    color: black;
  }

  .markdown blockquote{
     color: #428bca; 
    border-left: 5px solid #202938;
    padding-left: 0.6rem;
      margin-left:0rem;
 background-color:#ced0d6;
    border-radius: .4rem;
      width:100%;
  
  }



  .markdown h2 {
    max-width: 100%;
    width: 100%;
    overflow: hidden; 
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 1.75rem; /* 30px */
    line-height: 1.375;
    line-height: 2.25rem; /* 36px */
    margin-top: 3rem;   /* Equivalent to mt-12 in Tailwind CSS */
    margin-bottom: 1rem;
  }
  
  .markdown h3 {
    font-size: 1.4rem; /* 24px */
line-height: 2rem; /* 32px */
margin-top: 2rem;  /* Equivalent to mt-8 in Tailwind CSS */
margin-bottom: 1rem;
  }
